
.bar{
  width:500px;
  height:300px;
  border-bottom: 1px solid #000;
  border-left:1px solid #000;
  position: relative;
  left:100px;
  margin-bottom: 40px;

}
.yname{
  position: absolute;
  left: -70px;
  bottom: 10px;
  display: block;
  text-align: center;
  width: 100px;
}
.yLabel{
  width: 4px;
  position: absolute;
  left: -4px;
  bottom: 0;
  display: block;
  border-top: 1px solid #000;
}
.inner{
  width:50px;
  position: absolute;
  bottom: 0;
  background-color: red;
}

.innerValue{
  display: block;
  width:50px;
  position: absolute;
  bottom: 0;
  text-align: center;

}
.title{
  font-size: 15px;
  border-bottom: 1px solid #000;
  margin: 20px 0;
  margin-left: 100px;
  padding: 10px 0;
  width: 500px;
}





